// Grid
$grid-columns: 12;
$grid-space: 1rem;
$grid-space2x: calc(#{$grid-space} * 2);

// Fonts
$font-default: "Helvetica", Helvetica Neue, Arial, sans-serif;
$font-monospace: monospace, monospace;

$font-heading-1: 4.8rem;
$font-heading-2: 3.4rem;
$font-heading-3: 2.4rem;
$font-heading-4: 1.6rem;
$font-heading-5: 1.4rem;
$font-heading-6: 1.2rem;
$font-body: 1.6rem;

$font-weight-base: normal;

$line-height-base: 1.6;

$headings-margin-bottom: 3rem;
$paragraph-margin-bottom: 2rem;

$table-cell-padding: 1rem;

$label-margin-bottom: 1rem;

// Colors
$color-white: white;
$color-black: black;
$color-light-gray: #f7f7f7;
$color-mid-gray: #9f9f9f;
$color-dark-gray: #616161;

$link-color: blue;
$link-hover-color: darken(blue, 50%);
$link-decoration: none;
$link-hover-decoration: underline;

$table-caption-color: green;

// Borders
$border-width: 2px;
$border-style: solid;

$radius-circle: 50%;
$radius-rounded: .3rem;

$transition-slow: .8s ease;
$transition-normal: .4s ease;
$transition-fast: .2s ease;

$transition-all: .4s all;

$spacing-small: 3rem;
$spacing-base: 5rem;
$spacing-large: 8rem;

// Media Query Values
$mobile-only: (max-width: 599px);
$tablet-portrait-up: (min-width: 600px);
$tablet-landscape-up: (min-width: 900px);
$desktop-up: (min-width: 1200px);
$big-desktop-up: (min-width: 1800px);

$center: translate(-50%, -50%);
$centerY: translateY(-50%);
$centerX: translateX(-50%);